import React from 'react';
import ContentContainer from "./ContentContainer";
import Typography from "@material-ui/core/Typography";

export default function UnderConstruction(props) {
    function convertPathToModuleName() {
        let string = props.match.path.toString();
        let firstLetter = string.charAt(1).toUpperCase();
        string = firstLetter + string.slice(2, string.length);
        return string;
    }
    return (
        <ContentContainer title={convertPathToModuleName() + " wird erst noch erstellt."}>
            <Typography variant={"body2"} component={"div"}>
                Leider können wir dir diese Funktion noch nicht vorstellen, da sie gerade noch in Erstellung ist.
            </Typography>
        </ContentContainer>
    )
}
