import React, {useEffect} from 'react';
import { fade, makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import SideNavBar from './SideNavBar';
import {useDispatch, useSelector} from 'react-redux';
import Icon from '@material-ui/core/Icon'
import InputBase from '@material-ui/core/InputBase';
import ClickAwayListener from "@material-ui/core/ClickAwayListener";
import {Link} from "react-router-dom";
import Fade from "@material-ui/core/Fade";

const useStyles = makeStyles(theme => ({
    root: {
        flexGrow: 1,
    },
    menuButton: {
        marginRight: theme.spacing(2),
    },
    title: {
        flexGrow: 1,
    },
    spacer: {
        [theme.breakpoints.down('xs')]: {
            height: "56px"
        },
        [theme.breakpoints.up("sm")]: {
            height: "64px"
        }
    },
    inputInput: {
        padding: theme.spacing(1, 1, 1, 7),
        transition: theme.transitions.create('width'),
        width: '100%',
        [theme.breakpoints.up('md')]: {
            width: 400,
        }
    },
    inputRoot: {
        color: 'inherit'
    },
    search: {
        overflow: 'hidden',
        transition: 'all 0.5s ease',
        position: 'relative',
        borderRadius: theme.shape.borderRadius,
        backgroundColor: fade(theme.palette.common.white, 0.15),
        '&:hover': {
            backgroundColor: fade(theme.palette.common.white, 0.25),
        },
        margin: 0,

        '& input': {
            transition: "all 0.5s ease",
            width: "100px",
        },
        [theme.breakpoints.up('md')]: {
            marginLeft: theme.spacing(0),
            '& input': {
                width: '300px',
            }

        },
    },
    searchIcon: {
        width: theme.spacing(7),
        height: '100%',
        position: 'absolute',
        pointerEvents: 'none',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
    },
    alphaLogo: {
        height: "20px",
        marginLeft: theme.spacing(1),
        display: "none",
    },
    appBar: {
        transition: "box-shadow 0.3s ease"
    },
    appBarInitial: {
        boxShadow: 'none',
    },
    background: {
        position: 'absolute',
        height: "200px",
        width: "100%",
        top: 0,
        left: 0,
        backgroundColor: theme.palette.primary.main,
        zIndex: -1,
    },
    noStyleLink: {
        textDecoration: "none",
        color: "white",
    }
}));

export default function ButtonAppBar() {
    const [scrolled, setScrolled] = React.useState(false);
    const [search, setSearch] = React.useState(false);

    const loggedIn = useSelector(state => state.user.auth);

    const transitionDelay =  {
        enter: 333,
        exit: 333,
    };

    const handleScroll = () => {
        let windowTop = window.pageYOffset || document.documentElement.scrollTop;
        setScrolled(windowTop !== 0);
    };

    useEffect(() => {
        window.addEventListener('scroll', handleScroll);
        return () => {
            window.removeEventListener('scroll', handleScroll);
        }
    });
    const dispatch = useDispatch();

    const classes = useStyles();

    return (
        <div>
            <SideNavBar style={{position: "fixed"}}/>
            <div className={classes.background} />
            <div className={classes.root}>
                <AppBar position={"fixed"} className={(!scrolled ? classes.appBarInitial : " ") + " " + classes.appBar}>
                    <Toolbar>
                        <Fade in={loggedIn} unmountOnExit={true} timeout={transitionDelay} style={{transitionDelay: loggedIn ? '333ms': '0ms'}} mountOnEnter>
                            <IconButton edge="start" className={classes.menuButton} color="inherit" aria-label="Menu" onClick={() => dispatch({type: 'TOGGLE'})}>
                                <MenuIcon />
                            </IconButton>
                        </Fade>
                            <Typography variant="h6" style={{display: "flex", alignItems: "center", justifyContent: "flex-start"}} className={classes.title}>
                                <Link to={'/'} className={classes.noStyleLink}>
                                    <span style={{fontFamily: "Monoton"}}>
                                        Xise
                                    </span>
                                </Link>
                            </Typography>
                        <ClickAwayListener onClickAway={() => setSearch(false)}>
                            <Fade in={loggedIn} unmountOnExit={true} timeout={transitionDelay} style={{transitionDelay: loggedIn ? '333ms': '0ms'}} mountOnEnter>
                                <div className={classes.search} onClick={() => setSearch(true)}>
                                    <div className={classes.searchIcon}>
                                        <Icon>search</Icon>
                                    </div>
                                    <InputBase placeholder={"Suchen..."} classes={{root: classes.inputRoot, input: classes.inputInput}} inputProps={{ 'aria-label': 'Search' }} />
                                </div>
                            </Fade>
                        </ClickAwayListener>
                        <Fade in={!loggedIn} unmountOnExit={true} mountOnEnter={true} timeout={transitionDelay} style={{transitionDelay: !loggedIn ? '333ms': '0ms'}}>
                            <Link to={"/login"} className={classes.noStyleLink}>
                                <Button color="inherit">
                                    <Icon>exit_to_app</Icon>
                                    Login
                                </Button>
                            </Link>
                        </Fade>
                    </Toolbar>
                </AppBar>
            </div>
        </div>
    );
}
