import React from 'react';
import TextField from "@material-ui/core/TextField";
import { makeStyles } from '@material-ui/core/styles';
import Input from "@material-ui/core/Input";
import NativeSelect from "@material-ui/core/NativeSelect";
import InputLabel from "@material-ui/core/InputLabel";
import FormControl from "@material-ui/core/FormControl";

const useStyles = makeStyles(theme => ({
    input: {
        width: "150px",
    }
}));

export default function ExerciseInput(props) {
    const classes = useStyles();

    const [state, setState] = React.useState({
        unit: 1
    });

    const handleChange = name => event => {
        setState({
            ...state,
            [name]: event.target.value,
        })
    };

    function renderUnitSelect() {
        return (
            <FormControl className={classes.formControl}>
                <InputLabel htmlFor="age-native-helper">Präfix</InputLabel>
                <NativeSelect
                    value={state.unit}
                    onChange={handleChange('unit')}
                    input={<Input name="age" id="age-native-helper" />}
                >
                    <option value={0.000000000001}>p</option>
                    <option value={0.000000001}>n</option>
                    <option value={0.000001}>µ</option>
                    <option value={0.001}>m</option>
                    <option value={1}> </option>
                    <option value={1000}>k</option>
                    <option value={1000000}>M</option>
                    <option value={1000000000}>G</option>
                </NativeSelect>
            </FormControl>
        )
    }

    return (
        <div>
            <form onSubmit={() => {console.log(state)}} className={props.className}>
                <TextField id={props.id} label={props.placeholder} className={classes.input}/>
                {props.enableUnitSelect ? renderUnitSelect() : ""}
            </form>
        </div>
    );
}
