import React from 'react';
import Button from '@material-ui/core/Button';
import {makeStyles} from "@material-ui/core";
import Icon from "@material-ui/core/Icon";
import CircularProgress from "@material-ui/core/CircularProgress";

const useStyles = makeStyles(theme => ({
    transition: {
        transition: "all 0.5s ease",
    },
    root: {
        opacity: 1,
        width: 150,
        display: "inline-block",
    },
    submit: {
        width: 0,
        overflow: "hidden",
        opacity: 0,
    },
}));

export default function ReactiveSubmit(props) {
    const classes = useStyles();

    const {value, pending, success, className, ...rest} = props;

    return (
        <Button type={"submit"}
                variant={"contained"}
                size={"medium"}
                color={"primary"}
                className={classes.transition + ' ' + className}
                disabled={props.pending || !props.success}
                {...rest}
                >
                <div
                    className={`${classes.transition} ${classes.root} ${props.pending ? classes.submit : ""}`}
                    style={{display: "flex", justifyContent: "center", alignItems: "baseline"}}
                >
                    {props.value}
                    <Icon style={{fontSize: "0.8rem", paddingLeft: 5}}>arrow_forward_ios</Icon>
                </div>
                <CircularProgress
                    size={25}
                    className={`${classes.transition}`}
                    style={{opacity: props.pending ? 1 : 0, position: "absolute"}}
                    color={"primary"}
                />
        </Button>
    )
}
