import React from 'react';
import {makeStyles} from "@material-ui/core";
import Grid from '@material-ui/core/Grid';
import Icon from '@material-ui/core/Icon';
import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'
import ListItemText from '@material-ui/core/ListItemText'
import Container from '@material-ui/core/Container'
import Typography from '@material-ui/core/Typography'
import Button from '@material-ui/core/Button'

const useStyles = makeStyles(theme => ({
    footer: {
        backgroundColor: theme.palette.grey["200"],
        color: theme.palette.grey["500"],
        marginTop: "40px",
        boxShadow: "0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23)",
    },
    footerCopyright: {
        backgroundColor: theme.palette.grey["300"],
        padding: "5px",
        marginTop: 30,
    },
    footerCopyrightText: {
        fontSize: "8pt",
        display: "table-cell",
        verticalAlign: "middle",
        paddingLeft: 6
    },
    footerContent: {
        paddingTop: "20px",
        verticalAlign: "middle",
    },
    footerContentDisclaimer: {
        marginTop: "18px",
        paddingLeft: 8
    },
    footerCopyrightContainer: {
        display: "table",
    },
    button: {
        color: theme.palette.grey["500"],
        fontSize: "0.875rem",
        fontWeight: 400,
        fontFamily: "Roboto",
        letterSpacing: "0.01071rem",
        lineHeight: 1.43,
        textTransform: "none"
    }
}));

export default function Footer() {
    const classes = useStyles();

    return (
        <footer className={classes.footer}>
            <Container className={classes.footerContent}>
                <Grid container spacing={4} direction={"row"} alignItems={"flex-start"}>
                    <Grid item xs={12} md={7}>
                        <Typography variant={"h6"} component={"h6"}>
                            Preview
                        </Typography>
                        <Typography variant={"body2"} component={"p"} className={classes.footerContentDisclaimer}>
                            Diese App ist noch in Entwicklung, bei Fragen oder Verbesserungsvorschlägen wenden Sie sich über e-Mail an <a href={"mailto:ganzhort@hs-pforzheim.de"}>Tobias Ganzhorn</a>.
                        </Typography>
                    </Grid>
                    <Grid item xs={12} md={5}>
                        <div style={{display: "flex", flexDirection: "column", justifyContent: "flex-start", alignItems: "flex-start"}}>
                            <Typography variant={"h6"} component={"h6"}>
                                Links
                            </Typography>
                            <div style={{marginTop: 14}} />
                            <Button className={classes.button} size={"small"}>
                                Datenschutzerklärung
                            </Button>
                            <Button className={classes.button} size={"small"}>
                                Impressum
                            </Button>
                            <Button className={classes.button} size={"small"}>
                                Kontakt
                            </Button>
                        </div>
                    </Grid>
                </Grid>
            </Container>
            <div className={classes.footerCopyright}>
                <List disablePadding={true}>
                    <ListItem>
                        <ListItemText className={classes.footerCopyrightContainer}>
                            <Icon className={classes.footerCopyrightText}>copyright</Icon> <span className={classes.footerCopyrightText}>Tobias Ganzhorn, Tobias Rentschler | 2019</span>
                        </ListItemText>
                    </ListItem>
                </List>
            </div>
        </footer>
    )
}
