import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import SwipeableDrawer from '@material-ui/core/SwipeableDrawer';
import List from '@material-ui/core/List';
import Divider from '@material-ui/core/Divider';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import Avatar from '@material-ui/core/Avatar';
import {useSelector, useDispatch} from "react-redux";
import Badge from '@material-ui/core/Badge';
import Icon from '@material-ui/core/Icon';
import ListSubHeader from '@material-ui/core/ListSubheader'
import {Link} from 'react-router-dom';

const useStyles = makeStyles(theme => ({
    list: {
        [theme.breakpoints.down('xs')]: {
            width: "250px"
        },
        [theme.breakpoints.up("sm")]: {
            width: "400px"
        }
    },
    orangeAvatar: {
        color: '#fff',
        backgroundColor: theme.palette.secondary.main,
    },
    noStyleLink: {
        textDecoration: "none",
        color: "black",
    }
}));

export default function SwipeableTemporaryDrawer() {
    const dispatch = useDispatch();
    const open = useSelector(state => state.open);
    const user = useSelector(state => state.user);

    const classes = useStyles();

    function fullscreen() {
        let isInFullScreen = (document.fullscreenElement && document.fullscreenElement !== null) ||
            (document.webkitFullscreenElement && document.webkitFullscreenElement !== null) ||
            (document.mozFullScreenElement && document.mozFullScreenElement !== null) ||
            (document.msFullscreenElement && document.msFullscreenElement !== null);

        let docElm = document.documentElement;
        if (!isInFullScreen) {
            if (docElm.requestFullscreen) {
                docElm.requestFullscreen();
            } else if (docElm.mozRequestFullScreen) {
                docElm.mozRequestFullScreen();
            } else if (docElm.webkitRequestFullScreen) {
                docElm.webkitRequestFullScreen();
            } else if (docElm.msRequestFullscreen) {
                docElm.msRequestFullscreen();
            }
        } else {
            if (document.exitFullscreen) {
                document.exitFullscreen();
            } else if (document.webkitExitFullscreen) {
                document.webkitExitFullscreen();
            } else if (document.mozCancelFullScreen) {
                document.mozCancelFullScreen();
            } else if (document.msExitFullscreen) {
                document.msExitFullscreen();
            }
        }
    }

    const toggleDrawer = () => event => {
        if (event && event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
            return;
        }

        dispatch({type : 'TOGGLE'});
    };

    const sideList = () => (
        <div
            className={classes.list}
            role="presentation"
            onClick={toggleDrawer()}
            onKeyDown={toggleDrawer()}
        >
            <List disablePadding={true}>
                <ListSubHeader disableSticky={true}>
                    Profil
                </ListSubHeader>
                <Link to={'/profil'} className={classes.noStyleLink}>
                    <ListItem button>
                        <ListItemAvatar>
                            <Avatar className={classes.orangeAvatar}>{user.initials}</Avatar>
                        </ListItemAvatar>
                        <ListItemText primary={user.name} secondary={(user.name.toLowerCase().replace(" ", ".")) + "@live.de"} />
                    </ListItem>
                </Link>
                <Divider/>
                <ListSubHeader disableSticky={true}>
                    Inhalte
                </ListSubHeader>
                <Link to={'/formelsammlung'} className={classes.noStyleLink}>
                    <ListItem button>
                        <ListItemIcon><i className={"material-icons"}>widgets</i></ListItemIcon>
                        <ListItemText primary={"Formelsammlung"} />
                    </ListItem>
                </Link>
                <Link to={'/quiz'} className={classes.noStyleLink}>
                    <ListItem button>
                        <ListItemIcon><i className={"material-icons"}>school</i></ListItemIcon>
                        <ListItemText primary={"Quiz"} />
                    </ListItem>
                </Link>
                <Link to={'/kurse'} className={classes.noStyleLink}>
                    <ListItem button>
                        <ListItemIcon><i className={"material-icons"}>view_module</i></ListItemIcon>
                        <ListItemText primary={"Module"} />
                    </ListItem>
                </Link>
                <Divider />
                <ListSubHeader disableSticky={true}>
                    Kommunikation
                </ListSubHeader>
                <Link to={'/nachrichten'} className={classes.noStyleLink}>
                    <ListItem button>
                        <ListItemIcon>
                            <Badge color={"secondary"} badgeContent={3}>
                                <Icon>inbox</Icon>
                            </Badge>
                        </ListItemIcon>
                        <ListItemText primary={"Nachrichten"} />
                    </ListItem>
                </Link>
                <Divider />
                <ListSubHeader disableSticky={true}>
                    Tools
                </ListSubHeader>
                <ListItem button onClick={fullscreen}>
                    <ListItemIcon><i className={"material-icons"}>fullscreen</i></ListItemIcon>
                    <ListItemText primary={"Vollbild"} />
                </ListItem>
                <Link to={'/hilfe'} className={classes.noStyleLink}>
                    <ListItem button>
                        <ListItemIcon><i className={"material-icons"}>help</i></ListItemIcon>
                        <ListItemText primary={"Hilfe"} />
                    </ListItem>
                </Link>
                <Link to={'/einstellungen'} className={classes.noStyleLink}>
                    <ListItem button>
                        <ListItemIcon><i className={"material-icons"}>settings</i></ListItemIcon>
                        <ListItemText primary={"Einstellungen"} />
                    </ListItem>
                </Link>
                <Divider />
                <ListSubHeader disableSticky={true}>
                    Abmeldung
                </ListSubHeader>
                <Link to={'/logout'} className={classes.noStyleLink}>
                    <ListItem button onClick={() => dispatch({type: "LOGIN"})}>
                        <ListItemIcon><i className={"material-icons"}>logout</i></ListItemIcon>
                        <ListItemText primary={"Logout"} />
                    </ListItem>
                </Link>
            </List>
        </div>
    );

    return (
        <div>
            <SwipeableDrawer
                open={open}
                onClose={toggleDrawer()}
                onOpen={toggleDrawer()}
            >
                {sideList()}
            </SwipeableDrawer>
        </div>
    );
}
