import React from 'react';
import AppBar from './Components/AppBar';
import Footer from './Components/Footer';
import LandingPage from './Components/LandingPage';
import {BrowserRouter as Router, Redirect, Route, Switch} from 'react-router-dom';
import Exercise from './Components/Exercise';
import PrivateRoute from './Components/PrivateRoute';
import Profile from "./Components/Profile";
import UnderConstruction from "./Components/UnderConstruction";
import Login from "./Components/Login";
import ScrollToTop from "./Components/ScrollToTop";
import SwitchWithSlide from './Components/SwitchWithSlide';

function App() {
    return (
        <Router onChange={() => console.log("hi")}>
            <ScrollToTop>
            <div style={{display: "flex", minHeight: "100vh", width: "100%", overflow: "hidden", flexDirection: "column"}}>
                <AppBar />
                <div style={{flex: 1}}>
                    <SwitchWithSlide>
                        <Route path={'/'} exact component={LandingPage} />
                        <Route path={'/login'} exact component={Login} />
                        <PrivateRoute path={'/kurse'} component={Exercise} />
                        <PrivateRoute path={'/profil'} exact component={Profile} />
                        <PrivateRoute path={'/formelsammlung'} exact component={UnderConstruction} />
                        <PrivateRoute path={'/nachrichten'} exact component={UnderConstruction} />
                        <PrivateRoute path={'/hilfe'} exact component={UnderConstruction} />
                        <PrivateRoute path={'/einstellungen'} exact component={UnderConstruction} />
                        <PrivateRoute path={'/quiz'} component={UnderConstruction} />
                        <Redirect to={'/'} />
                    </SwitchWithSlide>
                </div>
                <Footer />
            </div>
            </ScrollToTop>
        </Router>
    );
}

export default App;
