import React from 'react';
import ContentContainer from "./ContentContainer";
import TextField from "@material-ui/core/TextField";
import {useDispatch, useSelector} from "react-redux";
import {Redirect} from "react-router-dom";
import ErrorSnackBar from "./ErrorSnackBar";
import ReactiveSubmit from "./ReactiveSubmit";

export default function Login() {
    const [state, setState] = React.useState({user: "", password: "", failedLogin: false});
    const [submit, setSubmit] = React.useState(false);

    const dispatch = useDispatch();
    const loggedIn = useSelector(state => state.user.auth);

    const handleChange = name => event => {
        setState({...state, [name] : event.target.value, failedLogin: false});
    };

    function renderError() {
        return <ErrorSnackBar message={"Login war nicht erfolgreich!"} open={state.failedLogin}/>
    }

    function authenticate(user, password) {
        return (user === 'Max Mustermann' && password === '123') ||
            (user === 'Tobias Rentschler' && password === '123') ||
            (user === 'Michael Felleisen' && password === '123') ||
            (user === 'Tobias Ganzhorn' && password === '123');
    }

    function testLogin() {
        setSubmit(true);
        setTimeout(handleLogin, 2000);
    }

    function handleLogin() {
        clearTimeout(handleLogin);

        if(authenticate(state.user, state.password)) {
            setState({...state, failedLogin: false});
            dispatch({type: "LOGIN", payload: {user: state.user, initials: getInitials(state.user)}});
        } else {
            setState({...state, failedLogin: true});
            setSubmit(false);
        }

    }

    function getInitials(glue) {
        let ok = false;

        if (typeof glue == "undefined") {
            ok = true;
        }

        let initials = glue.replace(/[^a-zA-Z- ]/g, "").match(/\b\w/g);

        if(ok) {
            if (glue) {
                return initials.join('').slice(0, 2);
            }
        }

        return (initials.slice(0, 2));
    }

    function renderRedirect() {
        if(loggedIn) return <Redirect to={'/'} />
    }

    return (
        <ContentContainer title={"Login"}>
            <form onSubmit={(event) => {testLogin(); event.preventDefault()}} style={{display: "flex", justifyContent: "center", alignItems: "center", flexWrap: "wrap", flexDirection: "column"}}>
                <TextField error={state.failedLogin}
                    id={"user"}
                    label="Benutzername"
                    type="username"
                    autoComplete="current-password"
                    margin="normal"
                    value={state.user}
                    required
                    onChange={handleChange("user")}
                    style={{margin: 6, width: "70%"}}
                />
                <TextField error={state.failedLogin}
                    id={"password"}
                    label="Passwort"
                    type="password"
                    autoComplete="current-password"
                    margin="normal"
                    value={state.password}
                    required
                    onChange={handleChange("password")}
                    style={{margin: 6, width: "70%"}}
                />
                <ReactiveSubmit value={"Einloggen"} pending={submit} success={!state.failedLogin} style={{marginTop: 12}}/>
            </form>
            {renderRedirect()}
            {/*renderError()*/}
        </ContentContainer>
    );
}
