const initialState = {
    open: false,
    user: {
        auth: false,
        status: "",
        email: "",
        name: "",
        initials: "",
        data: {},
    }
};

export default function rootReducer(state = initialState, action) {
    switch(action.type) {
        case 'TOGGLE' :
            return {...state, open: !state.open};
        case 'LOGIN' :
            if(state.user.auth) {
                return {...state,
                    user: {
                        ...state.user,
                        auth: false,
                    },
                }
            }

            return {...state,
                user: {
                    ...state.user,
                    auth: true,
                    name: action.payload.user,
                    initials: action.payload.initials,
                    email: action.payload.email,
                }
            };
        default :
            return state;
    }
}
