import React from 'react';
import { makeStyles} from "@material-ui/core/styles";
import ContentContainer from "./ContentContainer";
import Avatar from "@material-ui/core/Avatar";
import Typography from "@material-ui/core/Typography";
import {useSelector} from "react-redux";

const useStyles = makeStyles(theme => ({
    avatar: {
        backgroundColor: theme.palette.secondary.main,
        position: "absolute",
        padding: "10px",
        marginTop: "-50px",
        marginLeft: "-30px",
    },
    profile: {
        display: "flex",
        flexDirection: "row",
        alignItems: "center",
        justifyContent: "space-between",
        position: "relative",
    }
}));

export default function Profile() {
    const classes = useStyles();

    const user = useSelector(state => state.user);

    return (
        <ContentContainer title={<div className={classes.profile}><span>Profil</span><span><Avatar className={classes.avatar}>{user.initials}</Avatar></span></div>}>
            <Typography component={"h6"} variant={"h6"}>
                Hallo {user.name}
            </Typography>
            <Typography component={"div"} variant={"body2"}>
                Das hier ist dein Profil und an dieser Stelle stehen bald super spannende Dinge die du ändern kannst.
            </Typography>
        </ContentContainer>
    );
}
