import React from 'react';
import Typography from '@material-ui/core/Typography';
import Container from '@material-ui/core/Container'
import Box from '@material-ui/core/Box';
import Grid from '@material-ui/core/Grid';
import {makeStyles} from "@material-ui/core";
import Icon from '@material-ui/core/Icon'
import LandingPageComponent from "./LandingPageComponent";

const useStyles = makeStyles(theme => ({
    root: {
        padding: theme.spacing(3, 2),
    },
    background: {
        padding: "70px 0 70px 0",
        backgroundColor: theme.palette.primary.main,
        backgroundSize: "cover",
        backgroundPosition: "center, center",
        [theme.breakpoints.up("md")]: {
            padding: "120px 0 120px 0",
        }
    },
    secondBackground: {
        backgroundColor: theme.palette.grey["200"],
    },
    heading: {
        margin: "0 auto",
        textAlign: "center",
        fontFamily: '"Grand Hotel"',
        fontStyle: 'oblique',
        //mixBlendMode: "difference",
        color: "white",
        [theme.breakpoints.up("md")]: {
            fontSize: "5rem",
        }
    },
    paper: {
        backgroundColor: theme.palette.primary.main,
        transition: 'all 0.3s ease',
        overflow: "hidden",
        borderRadius: "2px",
    },
    grid: {
        marginTop: "10px",
    },
    paperHeading: {
        padding: "20px",
        color: "white"
    },
    paperContent: {
        padding: "5px 10px 5px 10px",
        backgroundColor: "white",
    },
    shower: {
        color: theme.palette.text.secondary,
        fontSize: "10pt",
        display: "table"
    },
    showerIcon: {
        display: "table-cell",
        verticalAlign: "middle",
        padding: "6px"
    },
    showerText: {
        display: "table-cell",
        verticalAlign: "middle",
        fontStyle: "bold"
    },
    headingAdornment: {
        fontFamily: "Roboto",
        fontSize: "12pt",
        [theme.breakpoints.up("md")]: {
            fontSize: "20pt"
        }
    }
}));

export default function LandingPage() {
    const classes = useStyles();

    return (
        <div>
            <Box className={classes.background}>
                <Container>
                    <Typography variant={"h2"} component={"h2"} className={classes.heading}>
                        Willkommen,<br /><span className={classes.headingAdornment}>bei <span style={{fontFamily: "Monoton", fontSize: "28pt"}}>Xise</span> der besten App rund um Elektrotechnik</span>
                    </Typography>
                </Container>
            </Box>
            <Box className={classes.secondBackground}>
                <Container>
                    <Grid container spacing={1} justify={"space-around"}>
                        <Grid item className={classes.shower}>
                            <Icon className={classes.showerIcon}>whatshot</Icon>
                            <span className={classes.showerText}>Schnell</span>
                        </Grid>
                        <Grid item className={classes.shower}>
                            <Icon className={classes.showerIcon}>school</Icon>
                            <span className={classes.showerText}>2 Fächer</span>
                        </Grid>
                        <Grid item className={classes.shower}>
                            <Icon className={classes.showerIcon}>touch_app</Icon>
                            <span className={classes.showerText}>Interaktiv</span>
                        </Grid>
                    </Grid>
                </Container>
            </Box>
            {/*<Container>
                <Grid container spacing={2} className={classes.grid}>
                    <Grid item xs={12} md={6}>
                        <Paper className={classes.paper}>
                            <Typography className={classes.paperHeading} variant={"h5"}>
                                Angebot
                            </Typography>
                            <Divider />
                            <Typography className={classes.paperContent} variant={"body2"} component={"p"}>
                                Wir bieten Ihnen eine interaktive und gute Möglichkeit, Ihr Wissen in Gleich- und Wechselstromtechnik zu vertiefen.
                            </Typography>
                            <Typography className={classes.paperContent} variant={"body2"} component={"p"}>
                                Falls Sie noch gar keine Kenntnisse in diesen Themengebieten haben bietet sich diese App auch zum erlernen der Grundkenntnisse an.
                                Sie werden dabei anhand mehrerer Untermodule durch die verschiedenen Theorien geführt. Zwischen jedem Untermodul gibt es eine kleine Zwischenprüfung,
                                die Sie absolvieren können falls Sie möchten.
                            </Typography>
                        </Paper>
                    </Grid>
                    <Grid item xs={12} md={6}>
                        <Paper className={classes.paper}>
                            <Typography className={classes.paperHeading} variant={"h5"} component={"h5"}>
                                Preise
                            </Typography>
                            <Divider />
                            <Typography className={classes.paperContent} variant={"body2"} component={"p"}>
                                Die App ist solange Sie in der Beta ist noch komplett kostenlos für Sie verfügbar.
                            </Typography>
                            <Typography className={classes.paperContent} variant={"body2"} component={"p"}>
                                Danach wird die App vermutlich für 4,99€ pro Monat oder 49,99€ im Jahr verfügbar sein, um diesen Preis rechtfertigen zu können
                                wird die Plattform natürlich immer wieder mit weiteren Lerninhalten gefüllt.
                            </Typography>
                        </Paper>
                    </Grid>
                    <Grid item xs={12} md={6}>
                        <Paper className={classes.paper}>
                            <Typography className={classes.paperHeading} variant={"h5"} color={"primary"}>
                                Über
                            </Typography>
                            <Divider />
                            <Typography className={classes.paperContent} variant={"body2"} component={"p"}>
                                Diese App entsteht im Rahmen einer Projektarbeit an der HS-Pforzheim, der Betreuer ist Prof.Dr.-Ing. Felleisen.
                            </Typography>
                            <Typography className={classes.paperContent} variant={"body2"} component={"p"}>
                                Entwickelt wird die App von Tobias Ganzhorn und Tobias Rentschler die beide im 4ten Semester im Studiengang Elektrotechnik/Informationstechnik sind.
                            </Typography>
                        </Paper>
                    </Grid>
                </Grid>
            </Container>*/}
            <LandingPageComponent heading={"Angebot"}>
                <Typography className={classes.paperContent} variant={"body2"} component={"p"}>
                    Wir bieten Ihnen eine interaktive und gute Möglichkeit, Ihr Wissen in Gleich- und Wechselstromtechnik zu vertiefen.
                </Typography>
                <Typography className={classes.paperContent} variant={"body2"} component={"p"}>
                    Falls Sie noch gar keine Kenntnisse in diesen Themengebieten haben bietet sich diese App auch zum erlernen der Grundkenntnisse an.
                    Sie werden dabei anhand mehrerer Untermodule durch die verschiedenen Theorien geführt. Zwischen jedem Untermodul gibt es eine kleine Zwischenprüfung,
                    die Sie absolvieren können falls Sie möchten.
                </Typography>
            </LandingPageComponent>
            <LandingPageComponent heading={"Preise"}>
                <Typography className={classes.paperContent} variant={"body2"} component={"p"}>
                    Die App ist solange Sie in der Beta ist noch komplett kostenlos für Sie verfügbar.
                </Typography>
                <Typography className={classes.paperContent} variant={"body2"} component={"p"}>
                    Danach wird die App vermutlich für 4,99€ pro Monat oder 49,99€ im Jahr verfügbar sein, um diesen Preis rechtfertigen zu können
                    wird die Plattform natürlich immer wieder mit weiteren Lerninhalten gefüllt.
                </Typography>
            </LandingPageComponent>
            <LandingPageComponent heading={"Über"}>
                <Typography className={classes.paperContent} variant={"body2"} component={"p"}>
                    Diese App entsteht im Rahmen einer Projektarbeit an der HS-Pforzheim, der Betreuer ist Prof.Dr.-Ing. Felleisen.
                </Typography>
                <Typography className={classes.paperContent} variant={"body2"} component={"p"}>
                    Entwickelt wird die App von Tobias Ganzhorn und Tobias Rentschler die beide im 4ten Semester im Studiengang Elektrotechnik/Informationstechnik sind.
                </Typography>
            </LandingPageComponent>
        </div>);
}
