import React from 'react';
import Typography from "@material-ui/core/Typography";
import MathJax from 'react-mathjax';
import makeStyles from "@material-ui/core/styles/makeStyles";
import ListItem from "@material-ui/core/ListItem";
import List from "@material-ui/core/List";
import ExerciseInput from './ExerciseInput';
import ContentContainer from "./ContentContainer";
import Button from "@material-ui/core/Button";
import ArrowForward from "@material-ui/icons/ArrowForward";

const useStyles = makeStyles(theme => ({
    solution: {
        margin: theme.spacing(2, 0, 2, 0),
        display: "flex",
        flexDirection: "row",
        width: "100%",
        justifyContent: "center",
        alignItems: "center",
        flexWrap: "wrap"
    },
    solutionButton: {
        padding: theme.spacing(2, 2, 2, 2)
    }
}));

export default function Exercise() {
    const classes = useStyles();

    return (
        <ContentContainer title={"Aufgabe 5.2"}>
            <MathJax.Provider>
                <Typography variant={"body1"} component={"div"}>
                    Ein Drehspulmesswerk misst an einer Wechselspannung von <MathJax.Node inline formula={'U = 220 V'} /> einen
                    Strom <MathJax.Node inline formula={'I = 2,5 mA'} />. Der verlustfreie, ideale Spulenkörper hat eine effektive Fläche
                    <MathJax.Node inline formula={'A = 23,6 cm^2'} />
                    . Mit der mittleren Feldlinienlänge <MathJax.Node inline formula={'l = 12,8 cm'} /> besitzt die Spule, die in
                    Öl (<MathJax.Node inline formula={'\\mu r = 1250'} />) gelagert ist, eine Induktivität von <MathJax.Node inline formula={'L = 14 mH'} />.
                    <List>
                        <ListItem>
                            <Typography variant={"body1"} component={"div"}>
                                a) Berechnen Sie den induktiven Blindwiderstand und die Frequenz der Wechselspannung.
                            </Typography>
                        </ListItem>
                        <ListItem>
                            <div className={classes.solution}>
                                <ExerciseInput className={classes.solutionButton} placeholder={"Blindwiderstand"} enableUnitSelect={true}/>
                                <ExerciseInput className={classes.solutionButton} placeholder={"Frequenz"} enableUnitSelect={false}/>
                            </div>
                        </ListItem>
                    </List>
                    Die Frequenz wird auf <MathJax.Node inline formula={'f = 60 kHz'} /> eingestellt; die Spannung verändert sich. Für den
                    Spulenstrom werden <MathJax.Node inline formula={'I = 3,8 mA'} /> gemessen. Die Induktivität bleibt bei <MathJax.Node inline formula={'L=14mH'} />.
                    <List>
                        <ListItem>
                            <Typography variant={"body1"} component={"div"}>
                                b) Bestimmen Sie den induktiven Blindwiderstand und Effektivwert der Spannung.
                            </Typography>
                        </ListItem>
                        <ListItem>
                            <div className={classes.solution}>
                                <ExerciseInput className={classes.solutionButton} placeholder={"Blindwiderstand"} enableUnitSelect={true}/>
                                <ExerciseInput className={classes.solutionButton} placeholder={"Effektivwert"} enableUnitSelect={false}/>
                            </div>
                        </ListItem>
                    </List>
                    <div style={{display: "flex", justifyContent: "center", marginTop: 10}}>
                        <Button variant={"contained"} color={"primary"}>Nächste Aufgabe <ArrowForward /></Button>
                    </div>
                </Typography>
            </MathJax.Provider>
        </ContentContainer>
    );
}
